import {
  Component,
  OnInit,
  Output,
  EventEmitter,
  Input,
  OnChanges
} from '@angular/core';
import * as _ from 'lodash';
import { ApiService } from '../api.service';
@Component({
  selector: 'app-sidenav',
  templateUrl: './sidenav.component.html',
  styleUrls: ['./sidenav.component.css']
})
export class SidenavComponent implements OnInit {
  @Output() opened = new EventEmitter<object>();
  @Input() dbs: any;
  isCollapsed: false;
  searchText: string;
  isInSearchMode: boolean;
  displayData: any[];
  constructor(private Api: ApiService) {
    this.displayData = [];
    this.searchText = '';
  }

  ngOnInit() {
    if (this.dbs) {
      this.filter();
    }
  }
  ngOnChanges() {
    if (this.dbs) {
      this.filter();
    }
  }

  filter() {
    this.isInSearchMode = true;
    this.displayData = _.cloneDeep(this.dbs.databases);
    if (!this.searchText) {
      this.isInSearchMode = false;
      return;
    }
    const pattern = new RegExp(`.*${this.searchText}.*`, 'gi');
    this.displayData = this.displayData
      .map(db => {
        db.collections = db.collections.filter((col: string) =>
          pattern.test(col)
        );
        return db.collections.length ? db : false;
      })
      .filter(Boolean);
  }

  openCollection(event, dbName, collectionName) {
    event.preventDefault();
    if (dbName && collectionName) {
      this.opened.emit({
        dbName,
        collectionName
      });
    }
  }
}
