import { Component, OnInit, Input } from '@angular/core';
import { ApiService } from '../api.service';
import { NzMessageService } from 'ng-zorro-antd/message';
import { serialize, deserialize, EJSON } from 'bson';

@Component({
  selector: 'app-collection-renderer',
  templateUrl: './collection-renderer.component.html',
  styleUrls: ['./collection-renderer.component.css']
})
export class CollectionRendererComponent implements OnInit {
  @Input() database: any;
  @Input() collection: any;
  data: object;
  filter: any;
  loading: boolean = false;
  pageIndex = 1;
  showEditor = false;
  documentBeingEdited: any;
  error: { status: boolean; desc: string } = { status: false, desc: '' };
  constructor(private API: ApiService, private message: NzMessageService) {}

  ngOnInit() {
    this.query();
  }

  query() {
    try {
      this.loading = true;
      const filter = this.filter ? JSON.parse(this.filter) : {};
      console.log(this.pageIndex);
      this.API.filterDocumentsByQuery(
        this.database,
        this.collection,
        filter,
        this.pageIndex
      ).subscribe((documents: any) => {
        this.loading = false;
        this.data = deserialize(Buffer.from(documents.data));
        console.log(this.data);
      });
    } catch (err) {
      console.log(err);
      this.loading = false;
    }
  }

  deleteDocument(id) {
    this.API.deleteDocumentById(this.database, this.collection, id).subscribe(
      () => {
        this.message.info('Deleted!');
        this.query();
      }
    );
  }

  updateDocument() {
    try {
      this.error.status = false;
      this.error.desc = '';
      const orignalDocument = serialize(
        EJSON.deserialize(JSON.parse(this.documentBeingEdited))
      );
      this.API.updateDocument(
        this.database,
        this.collection,
        orignalDocument
      ).subscribe(response => {
        console.log(response);
        this.closeEditor();
        this.query();
      });
    } catch (err) {
      this.error.status = true;
      this.error.desc = err;
    }
  }

  openEditor(doc): void {
    this.showEditor = true;
    this.documentBeingEdited = JSON.stringify(
      EJSON.serialize(doc),
      undefined,
      4
    );
  }

  closeEditor(): void {
    this.showEditor = false;
    this.documentBeingEdited = '';
  }

  copyToClipboard(text: string, msg: string) {
    text = JSON.stringify(text);
    const txtArea = document.createElement('textarea');
    txtArea.style.position = 'fixed';
    txtArea.style.top = '0';
    txtArea.style.left = '0';
    txtArea.style.opacity = '0';
    txtArea.value = text;
    document.body.appendChild(txtArea);
    txtArea.select();
    try {
      const result = document.execCommand('copy');
      if (result) {
        this.message.success('Copied!');
      }
    } catch (err) {}
    document.body.removeChild(txtArea);
  }
}
