export default ".container {\n  padding: 8px 10px;\n  overflow: auto;\n  height: calc(100vh - 140px);\n  background: #f5f5f5;\n}\n.document {\n  display: grid;\n  grid-template-columns: 9fr 3fr;\n  border: 1px solid #ddd;\n  background-color: #ffffff;\n  padding: 15px 8px;\n  margin-bottom: 8px;\n  /* box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),\n    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12) !important; */\n}\n.right [nz-button] {\n  margin: 0px 4px !important;\n  height: 25px;\n  width: 25px;\n}\n.document .right [nz-button] {\n  visibility: hidden;\n}\n.document:hover .right [nz-button] {\n  visibility: visible;\n}\n.query {\n  padding: 0px 10px;\n}\n.query [nz-input] {\n  background: whitesmoke;\n}\n.help {\n  border-bottom: 2px solid #ddd;\n  padding-bottom: 5px;\n}\n.tab_content {\n  grid-template-rows: auto;\n}\n.meta {\n  list-style-type: none;\n  display: -webkit-inline-box;\n  display: inline-flex;\n  padding: 0;\n}\n.meta li {\n  padding: 0px 15px;\n  vertical-align: middle;\n  line-height: 30px;\n}\n.bar {\n  display: grid;\n  grid-template-columns: 4fr 2fr;\n}\n.loader {\n  top: 100px;\n  position: relative;\n}\n.footer {\n  position: absolute;\n  bottom: 0px;\n  width: 100%;\n  border-top: 1px solid rgb(232, 232, 232);\n  padding: 10px 16px;\n  text-align: right;\n  left: 0px;\n  background: #fff;\n}\n.error_info {\n  margin-bottom: 20px;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"